.container {
  display: flex;
  flex-direction: column;
  align-items: center;

  gap: 24px;
}

.active {
  opacity: 1;
  transition: opacity 0.2s ease-in-out;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out;
}
.buttonCont {
  display: flex;
  justify-content: flex-end;
  transition: opacity 0.2s ease-in-out;
}
.inputContainer {
  height: 64px;
  padding: 0;
  border-style: none;

  display: flex;
  flex-direction: column;
}
